.dropdown {
  position: relative;
  display: inline-block;
  cursor:pointer !important;
}

.dropdown-content {
  display: none;
  cursor: pointer;
  position: absolute;
  background-color: #eee9e9;
  min-width: 160%;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 4% 7%;
  margin-left: auto;
  z-index: 10;
  font-size: 80%;
}
/* Add a class for selected items */
.selected {
  color: red; /* You can choose your desired color */
}

