.buttonstyle{
  background-color: #007bff; /* Background color of the button */
    color: #fff; /* Text color */
    padding: 10px 20px; /* Padding inside the button */
    border: none; /* No border */
    border-radius: 25px; /* Border radius to make corners rounded */
    cursor: pointer; /* Change cursor to a hand when hovering */
    font-size: 25px;
    /* Hover state */
    margin-top: 2%;
    transition: background-color 0.5s; /* Smooth transition */
}
