.headerStyles {
    background-color:  rgb(0, 91, 44);
    color: white;
    width: 100%;
    height: 4rem;
    margin-top: -1rem;
    padding-top: 1%;
    padding-bottom: 2%;
    cursor: pointer;
}

.headerStyles li{
    float: right;
    list-style: none;
    margin: 0.9rem;
    font-size: 1.1rem;
    margin-right: 3%;
    cursor:pointer !important;
    background-color: white;
    border-radius: 50px;
    padding: 4px 2%;
    text-decoration: none;
    color: black;
    font-weight: 500;
}
.headerStyles li:hover{
    background-color: #CCCCCC;
}
.headerStyles p{
    float: left;
    list-style: none;
    margin: 0.5rem;
    font-size: 2rem;
    font-weight: bold;
}
.headerStyles img{
    float: left;
    height: 5%;
    width: 5%;
    margin-top: 0.3rem;
}
.headerStyles ul{
    overflow: auto;
}
.headerStyles hr{
    display: none;
}
.hamburger {
    border: 0;
    width: 15%;
    padding: 0.5rem;
    border-radius: 50%;
    background-color: rgb(0, 91, 44);
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    /* position: absolute; */
    /* transform: translateY(-50%); */
    display: none;
    float: right;
    margin-right: 3%;
}
.hamburger:hover {
    background-color: #2642af;
}
.navigation-menu {
    margin-left: auto;
}
